// TechLevel Componet

import React from 'react'
import Icon from '../Icon'
function TechLevel (props) {
  let level = [];
  for(let i=0; i < 5; i++) {
      if( i < props.level) {
        level.push(<Icon iconName="star-full" key={i}/>)
      } else {
        level.push(<Icon iconName="star-empty" key={i} />)
      }
  }
  return(
    <figcaption>
      { level }
    </figcaption>
  )
}

export default TechLevel
